<template>
  <div class="evaluation-step">
    <div class="evaluation-step__item">
      Propreté
      <v-rating
          hover
          length="4"
          size="22"
          :value="0"
          color="#8abfdf"
          @input="ratingChange('cleanliness',$event)"
      ></v-rating>
    </div>

    <div  class="evaluation-step__item">
      Communication
      <v-rating
          hover
          length="4"
          size="22"
          :value="0"
          color="#8abfdf"
          @input="ratingChange('communication',$event)"
      ></v-rating>
    </div>

    <div  class="evaluation-step__item">
      Qualités personnelles
      <v-rating
          hover
          length="4"
          size="22"
          :value="0"
          color="#8abfdf"
          @input="ratingChange('personalQuality',$event)"
      ></v-rating>
    </div>
  </div>
</template>

<script>
export default {
  name: "EvaluationStep",
  props: ['value'],
  data() {
    return {
      evaluation: {}
    }
  },
  methods: {
    ratingChange(name, event) {
      if (name === 'cleanliness') {
        this.evaluation.cleanliness = event;
      }
      if (name === 'communication') {
        this.evaluation.communication = event;
      }
      if (name === 'personalQuality') {
        this.evaluation.personalQuality = event;
      }
      console.log()
      this.$emit('input', this.evaluation)
    }
  },
  mounted() {
    this.evaluation = {...this.value}
  }
}
</script>

<style lang="scss" scoped>
  .evaluation-step {
    display: flex;
    justify-content: space-around;
    width: 100%;

    &__item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
</style>