<template>
  <div class="avatar-select-container">
    <div class="avatar-select" :class="{ 'is-selected' : isSelected}" @click="$emit('change', value)">
      <img :src="require(`@/assets/avatars/${name}.svg`)">
    </div>
    <div>
      <slot></slot>
    </div>
  </div>

</template>

<script>
export default {
  name: "AvatarSelect",
  props: {
    isSelected: {
      type: Boolean
    },
    name: {
      type: String
    },
    value: {
      type: String
    }
  },
}
</script>

<style scoped lang="scss">
@import "@/assets/design/variable.scss";

$padding: 3px;

.avatar-select-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.avatar-select {
  display: flex;
  flex-direction: column;
  gap: 17px;

  height: 185px;
  width: 185px;

  padding: $padding;

  border-radius: 50%;
  border: 5px solid transparent;

  text-align: center;
}


.avatar-select:hover {
  cursor: pointer;

  &:not(.is-selected) {
    border: 5px solid $primary;
    padding: $padding;
  }
}

.is-selected {
  border: 5px solid $secondary;
  padding: $padding;
}
</style>