<template>
  <v-container>

    <v-row>
      <v-col cols="12">
        <div class="stepper-box">
          <v-stepper alt-labels v-model="step">
            <v-stepper-items>
              <v-stepper-content step="1">
                <content-step step="1" title="Choissisez votre style">
                  <PickCharacterStep v-model="form.character"/>
                </content-step>
              </v-stepper-content>
              <v-stepper-content step="2">
                <content-step step="2" title="Le type de logement">
                  <PickHouseTypeStep v-model="form.houseType"/>
                </content-step>
              </v-stepper-content>
              <v-stepper-content step="3">
                <content-step step="3" title="Votre évaluation">
                  <EvaluationStep v-model="form.evaluation"/>
                </content-step>
              </v-stepper-content>
              <v-stepper-content step="4">
                <content-step step="4" title="Quelques précisions sur votre séjour">
                  <PrecisionStep v-model="form.precision"/>
                </content-step>
              </v-stepper-content>
              <v-stepper-content step="5">
                <content-step step="5" title="Quelques précisions sur votre séjour">
                  <KeyWordsStep v-model="form.keywords"/>
                </content-step>
              </v-stepper-content>
              <v-stepper-content step="6">
                <content-step step="" title="Votre avis airbnb">
                  <ResultStep/>
                </content-step>
              </v-stepper-content>
            </v-stepper-items>
          </v-stepper>

          <div class="stepper-box__action" :class="{'stepper-box__action--space-between': step > 1}">
            <button-widget v-if="step > 1" @click.native="step--">Précedent</button-widget>
            <button-widget v-if="step < 6" @click.native="step++" :disabled="!checkValidity">Suivant</button-widget>
            <button-widget v-if="step === 6" @click.native="step=1">Retour question 1</button-widget>
          </div>
        </div>

      </v-col>

    </v-row>

  </v-container>
</template>

<script>
import ButtonWidget from "@/components/ButtonWidget";
import PickCharacterStep from "@/components/PickCharacterStep";
import ContentStep from "@/components/ContentStep";
import PickHouseTypeStep from "@/components/PickHouseTypeStep";
import EvaluationStep from "@/components/EvaluationStep";
import PrecisionStep from "@/components/PrecisionStep";
import KeyWordsStep from "@/components/KeyWordsStep";
import ResultStep from "@/components/ResultStep";

export default {
  name: 'AirbnbReviewPage',
  components: {
    ResultStep,
    KeyWordsStep,
    PrecisionStep,
    EvaluationStep, PickHouseTypeStep, ContentStep, PickCharacterStep, ButtonWidget
  },
  data: () => ({
    step: 1,
    form: {
      character: '',
      houseType: '',
      evaluation: {
        cleanliness: -1,
        communication: -1,
        personalQuality: -1,
      },
      precision: {
        name: "",
        location: "",
        duration: 0
      },
      keywords: ""
    }
  }),
  computed: {
    checkValidity() {
      if (this.step === 1 && this.form.character) {
        return true
      }
      if (this.step === 2 && this.form.houseType) {
        return true
      }
      if (this.step === 3
          && this.form.evaluation.cleanliness > -1
          && this.form.evaluation.communication > -1
          && this.form.evaluation.personalQuality > -1
      ) {
        return true
      }
      if (this.step === 4
          && this.form.precision.name
          && this.form.precision.duration
          && this.form.precision.location
      ) {
        return true
      }
      if (this.step === 5 && this.form.keywords) {
        return true
      }
      return false
    }
  }
}
</script>

