<template>
  <div class="precision-step">
    <div class="precision-step__field">
      <v-text-field
          label="Prénom de l'hôte"
          placeholder="Cédric"
          filled
          rounded
          @input="updateValue('name', $event)"
      ></v-text-field>
    </div>

    <div class="precision-step__field">
      <v-text-field
          label="Localisation"
          placeholder="Nom de la ville"
          filled
          rounded
          append-icon="mdi-map-marker"
          @input="updateValue('location', $event)"
      ></v-text-field>
    </div>

    <div class="precision-step__field">
      <v-text-field
          label="Nombre de nuit"
          type="number"
          placeholder="5"
          filled
          rounded
          min="1"
          @input="updateValue('duration', $event)"
      ></v-text-field>
    </div>
  </div>
</template>

<script>
export default {
  name: "PrecisionStep",
  props: ['value'],
  methods: {
    isNumeric(str) {
      if (typeof str != "string") return false // we only process strings!
      return !isNaN(str) && // use type coercion to parse the _entirety_ of the string (`parseFloat` alone does not do this)...
          !isNaN(parseFloat(str)) // ...and ensure strings of whitespace fail
    },
    updateValue(name, $event) {
      const precision = {...this.value}
      if (name === 'name' || name === 'location' ) {
        precision[name] = $event;
      }
      else if (name === 'duration' && this.isNumeric($event)) {
        precision[name] = $event;
      } else  {
        precision[name] = null;
      }
      this.$emit('input', precision)
    }
  }
}
</script>

<style lang="scss" scoped>
.precision-step {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;

  &__field {

  }
}
</style>